.About {
  padding: 1rem;
  display: flex;
  flex-direction: row;

  /* Extra small devices (phones, 600px and down) */
  @media only screen and (max-width: 600px) {
    padding: 1rem;
  } 

  &_Image-container {
    display: block;
    margin: auto auto; 
  }

  &_Image {
    border-radius: 10rem;
  }

  &_Intro {
    margin: 1rem;
  }
}
